import { getScale } from '@antv/scale';
import * as _ from '@antv/util';
import { getMean, getMedian } from '../util/math';

var GuideLine =
/** @class */
function () {
  function GuideLine(cfg) {
    _.assign(this, cfg);

    this._init();
  }

  GuideLine.prototype._init = function () {
    var props = this.plot.options;

    var defaultStyle = this._getDefaultStyle();

    var baseConfig = {
      type: 'line',
      top: true,
      start: this.cfg.start,
      end: this.cfg.end
    };
    baseConfig.line = _.deepMix({}, defaultStyle.line, {
      style: this.cfg.lineStyle
    });
    baseConfig.text = _.deepMix({}, defaultStyle.text, this.cfg.text);

    if (this.cfg.type) {
      var stateValue = this._getState(this.cfg.type);

      var minValue = this._getState('min');

      var maxValue = this._getState('max');

      var Scale = getScale('linear'); // scalescaleminmaxguide pointnice

      var scale = new Scale(_.mix({}, {
        min: minValue,
        max: maxValue,
        nice: true
      }, props.meta));
      var percent = (stateValue - scale.min) / (scale.max - scale.min) * 100 + "%";
      var start = ['0%', percent];
      var end = ['100%', percent];
      this.config = _.mix({
        start: start,
        end: end
      }, baseConfig);
    } else {
      this.config = baseConfig;
    }
  };

  GuideLine.prototype._getState = function (type) {
    var values = this._extractValues();

    if (type === 'median') {
      return getMedian(values);
    }

    if (type === 'mean') {
      return getMean(values);
    }

    if (type === 'max') {
      return Math.max.apply(Math, values);
    }

    if (type === 'min') {
      return Math.min.apply(Math, values);
    }
  };

  GuideLine.prototype._extractValues = function () {
    var props = this.plot.options;
    var field = props.yField;
    var values = [];

    _.each(props.data, function (d) {
      values.push(d[field]);
    });

    return values;
  };

  GuideLine.prototype._getDefaultStyle = function () {
    return {
      line: {
        style: {
          lineWidth: 2,
          stroke: '#333333',
          opacity: 0.7,
          lineDash: [0, 0]
        }
      },
      text: {
        offsetY: -5,
        style: {
          fontSize: 14,
          stroke: 'white',
          lineWidth: 2
        }
      }
    };
  };

  return GuideLine;
}();

export default GuideLine;